.dash-container {
  width: 100%;
  height: 100vh;
  background: linear-gradient( rgba(0, 0, 0, 0.5) , rgba(0, 0, 0, 0.5) ), url(/bank-app/static/media/bgimage3.3819d059.jpg);
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  width: 100%;
  max-width: 150rem;
  height: 80vh;
  background-color: #2c2c2d;
  color: #eee;
  border-radius: 0.5rem;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.7);
  display: grid;
  grid-template-columns: minmax(38%, 40rem) repeat(2, minmax(29%, 29rem));
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 1rem;
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

.dash-head-container {
  grid-column: 1 / 2;
  grid-row: 1 / 4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dash-head-container h1 {
  font-family: 'STIX Two Text', serif;
  font-size: 6rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: whitesmoke;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.balance {
  grid-column: 1 / 1;
  grid-row: 3 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
  text-align: center;
}

.balance h2 {
  font-family: 'STIX Two Text', serif;
  font-size: 4rem;
  font-weight: 200;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.balance h3 {
  font-family: 'STIX Two Text', serif;
  font-size: 4rem;
  font-weight: 300;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

